<script setup>
    import { onMounted, ref } from 'vue';
    import Button from './../../components/Button/';
    import { useRouter } from 'vue-router';
    import File from './images/file.svg';
    import ButtonFiles from './components/ButtonFiles.vue';
    import ItemList from './components/ItemList.vue';
    import post from '../../utils/post';
    import { useStore } from 'vuex';

    const store = useStore();
    const selectedPage = ref('received');
    
    const passportInput = ref('');
    const invoiceValue = ref('');
    const reasonValue = ref(''); 

    async function handleClick(){
        const passport = parseInt(Math.abs(passportInput.value));
        const value = parseInt(Math.abs(invoiceValue.value));
        const reason = reasonValue.value;
        
        if(passport && value && reason){
            const res = await post('MakeInvoice', { passport, value, reason });
            if(res){
                passportInput.value = '';
                invoiceValue.value = '';
                reasonValue.value = '';
                store.state.initial.InvoiceList = res;
            }
        }
    }

    async function handleRemove(id){
        if(selectedPage.value === 'received'){
            const res = await post('InvoicePayment', { id });
            if(res){
                const index = store.state.initial.InvoiceList[selectedPage.value].findIndex(el => el.id === id);
                store.state.initial.InvoiceList[selectedPage.value].splice(index, 1);
            }
        }
    }
</script>

<template>
    <div class="file">
        <p>Fatura</p>
        <div class="main-file">
            <div class="input-frame">
                <form @submit.prevent="">
                    <div class="form-frame">
                        <label>Passaporte</label>
                        <input placeholder="0" v-model="passportInput" type="number" />
                    </div>
                    <div class="form-frame">
                        <label>Valor da Fatura</label>
                        <input placeholder="0" v-model="invoiceValue" type="number" />
                    </div>
                    <div class="form-frame">
                        <label>Razão</label>
                        <input placeholder="Razão da fatura" v-model="reasonValue" type="text" />
                    </div>
                    <Button :cb="handleClick" class="button" text="CRIAR FATURA" :img="File" />
                </form>
            </div>
            <div class="list-frame">
                <div class="header-list-frame">
                    <ButtonFiles :cb="() => selectedPage = 'received'" :selected="selectedPage === 'received'" type="received"/>
                    <ButtonFiles :cb="() => selectedPage = 'sent'" :selected="selectedPage === 'sent'" type="sent"/>
                </div>
                <div class="list-container">
                    <div class="list-inside" v-if="store.state.initial.InvoiceList">
                        <ItemList :cb="handleRemove" v-for="(item, key) in store.state.initial.InvoiceList[selectedPage]" :hasPayment="selectedPage == 'received'" :data="item" :key='key'/>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

