        <div class="bar" v-if="selected"></div>
    </div>
</template>

<style scoped>

    .button-file {
        cursor: pointer;
        transition: all ease .4s;
        opacity: .25;
    }

    .selected {
        opacity: 1;
    }

    .button-file:hover {
        opacity: .7;
    }
    
    .bar {
        width: 20%;
        height: 0.052083333333333336vw;
        background-color: white;
    }
</style>