<script setup>
    import { defineProps } from 'vue';
    import formatMoney from '../../../utils/formatMoney';

    const props = defineProps({
        cb: Function,
        data: Object,
        hasPayment: Boolean
    });

    function handleClick(){
        props.cb(props.data.id);
    }
</script>

<template>
    <div class="item-list" :class="{'notPayment': !hasPayment}" @click="handleClick">
        <p class="pay-invoice-text">Pagar</p>
        <div class="left-item">
            <p>{{props.data.reason}}</p>
            <span>{{props.data.holder}}</span>
        </div>
        <span>{{formatMoney(props.data.value)}}</span>
    </div>
</template>

